const permutation = (arr) => {
	let permutations = []

	const swap = (a, b) => {
		let tmp = arr[a];
		arr[a] = arr[b];
		arr[b] = tmp;
	}

	const generate = (n) => {
		if(n === 1){
			let newArr = arr.slice(0, 2)
			permutations.push(newArr)
		}else{
			for(let i = 0; i !== n; ++i){
				generate(n - 1);
				swap(n % 2 ? 0 : i, n - 1)
			}
		}
	}

	generate(arr.length)
	return permutations
}

export default permutation


